import React, { useState, useEffect } from "react";

const InputBar = ({
  list,
  setList,
  setPercent,
  countChecked,
  percent,
  totalItem,
  setTotalItem,
}) => {
  const [value, setValue] = useState("");

  // Input_bar  value
  const changeHandler = (e) => {
    setValue(e.target.value);
  };

  const addHandler = () => {
    //let  newItem  content  value
    let newItem = {
      content: value,
    };
    //let  updateList  list  newItem  updateList    SetList  list  Item
    let updateList = [...list];
    updateList.push(newItem);
    console.log(updateList);
    setList(updateList);
    setTotalItem(updateList.length);
  };

  //InputvalueValue
  const saveHandler = () => {
    value === "" ? alert("") : addHandler(value);
    setValue("");
  };

  //
  useEffect(() => {
    console.log("totalItem:" + totalItem);
    console.log("Checked:" + countChecked);
    setPercent(Math.round((countChecked / totalItem) * 100));
    console.log("" + percent);
  }, [countChecked, totalItem, percent]);

  return (
    <div className="inputBar">
      <input onChange={changeHandler} value={value} />
      <button onClick={saveHandler}></button>
    </div>
  );
};

export default InputBar;
