import React, { useState, useRef, useEffect } from "react";
import InputBar from "./InputBar";
import ScrollView from "./ScrollView";
import ProgressBar from "./ProgressBar";
import ItemList from "./ItemList";

const TodoList = () => {
  const [list, setList] = useState([]);
  const [percent, setPercent] = useState(0);
  const [countChecked, setCountChecked] = useState(0);
  const [totalItem, setTotalItem] = useState(0);
  const contentRef = useRef([]);

  //let  deleteList  list ,  splice  index  Item setList  list 
  const deleteHandler = (index) => {
    let deleteList = [...list];
    deleteList.splice(index, 1);
    console.log(deleteList);
    setList(deleteList);
    setTotalItem(deleteList.length);
  };

  const checkHandler = (index) => (e) => {
    if (e.target.checked) {
      console.log(" Checkbox is checked");
      contentRef.current[index].style.textDecoration = "line-through";
      setCountChecked((prev) => prev + 1);
    } else {
      console.log(" Checkbox is NOT checked");
      contentRef.current[index].style.textDecoration = "none";
      setCountChecked((prev) => prev - 1);
    }
  };

  useEffect(() => {
    console.log("totalItem:" + totalItem);
    console.log("Checked:" + countChecked);
    setPercent(Math.round((countChecked / totalItem) * 100));
    console.log("" + percent);
  }, [countChecked, totalItem, percent]);

  return (
    <div className="todoList">
      <h1 className="title">Todo List</h1>
      <ProgressBar percent={percent} />
      <ItemList>
        <ScrollView>
          {/* map  list  Item  key*/}
          {Array.from(list).map((item, index) => {
            return (
              <div className="item" key={index}>
                <input
                  type="checkbox"
                  name="done"
                  onChange={checkHandler(index)}
                />
                <div
                  className="content"
                  ref={(el) => (contentRef.current[index] = el)}
                >
                  {item.content}
                </div>
                <button
                  className="delBtn"
                  onClick={() => deleteHandler(index)}
                ></button>
              </div>
            );
          })}
        </ScrollView>
      </ItemList>
      <InputBar
        list={list}
        setList={setList}
        percent={percent}
        setPercent={setPercent}
        totalItem={totalItem}
        setTotalItem={setTotalItem}
        countChecked={countChecked}
      />
    </div>
  );
};

export default TodoList;
